import React from 'react'

import { convert } from '../../utils/conversions'

export default function UserStatistics(props) {
  const { t, user } = props
  const days = user.total_duration.match(/day/g)
    ? `${user.total_duration.split(' ')[0]} ${
        user.total_duration.match(/days/g) ? t('common:days') : t('common:day')
      }`
    : `0 ${t('common:days')},`
  let duration = user.total_duration.match(/day/g)
    ? user.total_duration.split(', ')[1]
    : user.total_duration
  duration = `${duration.split(':')[0]}h ${duration.split(':')[1]}min`
  return (
    <div className="row">
      <div className="col-lg-3 col-md-6 col-sm-6">
        <div className="card workout-card">
          <div className="card-body row">
            <div className="col-3">
              <i className="fa fa-calendar fa-3x fa-color" />
            </div>
            <div className="col-9 text-right">
              <div className="huge">
                {user.nb_workouts} ({user.nb_sports})
              </div>
              <div>
                {`${
                  user.nb_workouts === 1
                    ? t('common:workout')
                    : t('common:workouts')
                }`}{' '}
                (
                {`${
                  user.nb_sports === 1 ? t('common:sport') : t('common:sports')
                }`}
                )
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-3 col-md-6 col-sm-6">
        <div className="card workout-card">
          <div className="card-body row">
            <div className="col-3">
              <i className="fa fa-road fa-3x fa-color" />
            </div>
            <div className="col-9 text-right">
              <div className="huge">
                {convert(user.total_distance, t('common:km'))}
              </div>
              <div>{t('common:km')}</div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-3 col-md-6 col-sm-6">
        <div className="card workout-card">
          <div className="card-body row">
            <div className="col-3">
              <i className="fa fa-arrow-circle-up fa-3x fa-color" />
            </div>
            <div className="col-9 text-right">
              <div className="huge">
                {convert(user.total_ascent, t('common:m'))}
              </div>
              <div>{t('common:m')}</div>
            </div>
          </div>
        </div>
      </div>
      <div className="col-lg-3 col-md-6 col-sm-6">
        <div className="card workout-card">
          <div className="card-body row">
            <div className="col-3">
              <i className="fa fa-clock-o fa-3x fa-color" />
            </div>
            <div className="col-9 text-right">
              <div className="huge">{days}</div>
              <div>{duration}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
